  position: relative;
  height: 100%;
  width: 100%;
  color: #ededed;
  font-family: "Roboto", sans-serif;
  font-size: 1.2rem;
}

.innerContainer {
  position: relative;
  height: 100%;
  width: 100%;
}

.textShadowLight {
  text-shadow: 2px 2px 2px black;
}

.textShadowLighter {
  text-shadow: 1px 1px 1px black;
}

.Opacity-In {
  animation: opacity 0.5s ease-in-out;
  animation-fill-mode: forwards;
}

.Opacity-Out {
  animation: opacity-reverse 0.5s ease-in-out;
  animation-fill-mode: backwards;
}

@keyframes hover-up-down {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes opacity-reverse {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes hover-up-down-3D {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3.5px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes navLinkBlueShadow {
  0% {
    box-shadow: 0 0 0px #287aff;
  }
  50% {
    box-shadow: 0 0 8px #287aff;
    text-shadow: 0px 0px 10px #287aff;
  }
  100% {
    box-shadow: 0 0 0px #287aff;
  }
}

@keyframes slide-in-left {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}

.inlineOrangeText {
  color: #f4a227;
  text-shadow: 2px 2px 2px black;
  display: inline;
}

.inlineBlueText {
  color: #24b0df;
  text-shadow: 2px 2px 2px black;
  display: inline;
}

.inlineRedText {
  color: #db3523;
  text-shadow: 2px 2px 2px black;
  display: inline;
}

.inlineWhiteText {
  color: #ededed;
  text-shadow: 2px 2px 2px black;
  display: inline;
}

.page {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;
  min-height: 30vh;
  min-width: 25vw;
  border-radius: 20px;
}

.page.dark {
  border: 1px solid rgb(255, 255, 255);
}

.page.light {
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.page h1 {
  color: white;
